<template>
  <div>
    <v-card max-width="400" class="news__users__reg">
      <v-card-title>Зарегистрировались недавно</v-card-title>
    </v-card>
    <v-container class="news__users" v-if="firstUsers != []">
      <v-row cols="4" sm="2">
      <v-col v-for="(user, index) of firstUsers" :key="index">
          <v-card v-if="user.id != myAccount.id" class="news__users__item">
            <v-img 
              class="news__users__logo"
              v-if="user.additionally.img" 
              :src="'/' + user.additionally.img"
            ></v-img>
            <div class="news__users__logo" v-else></div>
            <router-link 
              class="news__users__link" 
              :to="{ name: 'Account', params: { id: user.id }}"
            >
              <v-card-title>{{ user.firstname }} {{ user.lastname }}</v-card-title>
            </router-link>
            <v-chip style="cursor: pointer" color="myPurple" text-color="white">
              {{ user.level }} {{ user.rank }}
            </v-chip>
            <v-btn class="news__users__btn" color="success" @click="chatCreate(user)">
              Написать сообщение
            </v-btn>
            <v-btn 
              v-if="myAccount.friends.includes(user.id)" 
              class="news__users__btn" 
              color="error"
              @click="friendDelete(user)"
            >
              Удалить из друзей
            </v-btn>
            <v-btn 
              v-if="!myAccount.subscrise.includes(user.id) && !user.subscrise.includes(myAccount.id)" 
              @click="friendInvite(user)" 
              class="news__users__btn"
              color="myPurple"
            >
              Добавить в друзья
            </v-btn>
            <v-btn 
              v-if="user.subscrise.includes(myAccount.id)" 
              @click="subDelete(user)" 
              class="news__users__btn"
              color="myPurple"
            >
              Отменить заявку в друзья
            </v-btn>
        </v-card>
      </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "NewsFriend",
  data() {
    return {
      firstUsers: [],
      fullUsers: [],
      myAccount: ""
    }
  },
  props: {
    user: Object
  },
  async created() {
    this.myAccount = this.user
    let res = await axios.get("/api/users/getNewUsers/" + this.user.id)
    this.firstUsers = res.data.firstUsers
    this.fullUsers = res.data.fullUsers
  },
  methods: {
    async chatCreate(user) {
      let res = await axios.post("/api/chat/chat-create", 
        { userId: user.id, myId: this.user.id })
      this.$router.push({ name: 'Chat', params: { id: res.data.chatId }})
    },
    async friendInvite(addUser) {
      await axios.put("/api/users/friend_add/" + addUser.id + '/' + this.myAccount.id)
    },
    async friendDelete(delUser) {
      await axios.delete("/api/users/friend_remove/" + 
        delUser.id + '/' + this.myAccount.id) 
    },
    async subDelete(user) {
      await axios.delete("/api/users/friend_cancel/" + user.id + '/' + this.myAccount.id)
    },
  }
};
</script>

<style lang="scss" scoped>
.news {
  &__users {
    margin-top: 20px;
    margin-left: 20px;
    margin-bottom: 50px;
    // display: grid;
    // grid-template-columns: repeat(3, 1fr);
    // grid-gap: 50px;
    &__item {
      height: 400px;
      max-width: 300px;
      min-width: 250px;
      display: flex;
      align-items: center;
      flex-direction: column;
      padding: 20px;
    }
    &__logo {
      min-width: 128px;
      max-width: 128px;
      min-height: 128px;
      max-height: 128px;
      border-radius: 50%;
      cursor: pointer;
      background-color: #2cb67d;
    }
    &__btn {
      margin-top: 30px;
      color: #fff !important;
    }
    &__link {
      text-decoration: none !important;
    }
    &__reg {
      margin-left: 20px;
      margin-top: 40px;
    }
  }
}
</style>
