<template>
  <div>
    <NotAuth>
      <template v-slot:error></template>
      <template v-slot:loading></template>
      <template v-slot:default>
        <div class="news__wrapper d-flex">
          <div style="margin-left: 20px">
            <Panel />
          </div>
          <div class="d-flex flex-column">
            <v-toolbar v-if="dataDB" class="news__top">
              <h2>Моя лента</h2>
            </v-toolbar>
            <v-card v-else>
              <v-card-title class="display-1">
                Добавьте друзей, чтобы просматривать их посты.
              </v-card-title>
            </v-card>
            <NewsFriend :user="user" />
            <PostNews @isData="isData" :dataDB="user" />
          </div>
          <div class="news__nav">
            <h2>Здесь что-то будет</h2>
            <v-img max-width="200" max-height="200" src="../assets/error.jpg"></v-img>
          </div>
        </div>
      </template>
    </NotAuth>
  </div>
</template>

<script>
import PostNews from "../components/PostNews.vue";
import NewsFriend from "../components/NewsFriend.vue";
import NotAuth from "../components/NotAuth.vue";
import Panel from "../components/Panel.vue";

export default {
  name: "News",
  metaInfo: {
    title: "MyOcean - Моя лента"
  },
  components: { PostNews, NewsFriend, NotAuth, Panel },
  data() {
    return {
      user: "",
      dataDB: ""
    }
  },
  created() {
    this.user = JSON.parse(localStorage.getItem("user"))
  },
  sockets: {},
  methods: {
    isData(data) {
      if (data.length > 0) this.dataDB = true
      else this.dataDB = false
    }
  }
};
</script>

<style lang="scss" scoped>
.news {
  width: 1170px;
  min-height: 500px;
  &__wrapper {
    margin-top: 100px;
  }
  &__top {
    margin-left: 20px;
  }
  &__nav {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-left: 60px;
    width: 250px;
    height: 400px;
    color: #424242;
    background-color: #fff;
    border-radius: 6px;
  }
}
@media (max-width: 1024px) {
  .news__nav {
    display: none;
  }
}
</style>
